import React from 'react';
import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Avatar, PanelHeader, Counter, List, Cell } from '@vkontakte/vkui';

const Home = ({ id, go, fetchedUser, userData }) => (
	<Panel id={id}>
		<PanelHeader>Информация об участнике</PanelHeader>
		{fetchedUser &&
		<Group title="User Data Fetched with VK Connect">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200} size={10}/> : null}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		{userData &&
		<Group title="Баллы">
			<List>
				<Cell indicator={<Counter type="primary">1</Counter>}>Друзья</Cell>
			</List>
		</Group>}
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
